import React from 'react'
import ReactDOM from 'react-dom'
import SortableTree, { toggleExpandedForAll } from 'react-sortable-tree'
import { ThemeProvider } from '@material-ui/styles'
import { createMuiTheme } from '@material-ui/core/styles'
//import TreeTest from './TreeTest'
import treeData from './TreeTest/testData'
import './styles.css'
import 'react-sortable-tree/style.css'
import MoreMenu from './TreeTest/MoreMenu'
import treeTheme from './TreeTest/theme'

const mytheme = createMuiTheme({
  palette: {
    type: 'light',
  },
})
window.mui = {}

class App extends React.Component {
  state = {
    searchString: '',
    searchFocusIndex: -1,
    searchFoundCount: 0,
    treeData,
  }

  constructor() {
    super()
    window.mui.setNewData = this.setNewData
  }

  handleTreeOnChange = (treeData) => {
    this.setState({ treeData })
    //mui.onTreeChange(treeData)
  }

  handleSearchOnChange = (e) => {
    //console.log({ e })
    this.setState({
      searchString: e.target.value,
    })
  }

  setNewData = (newTreeData) => {
    this.setState({ treeData: newTreeData })
    this.toggleNodeExpansion(true)
  }

  selectPrevMatch = () => {
    const { searchFocusIndex, searchFoundCount } = this.state

    this.setState({
      searchFocusIndex:
        searchFocusIndex !== null ? (searchFoundCount + searchFocusIndex - 1) % searchFoundCount : searchFoundCount - 1,
    })
  }

  selectNextMatch = () => {
    const { searchFocusIndex, searchFoundCount } = this.state

    this.setState({
      searchFocusIndex: searchFocusIndex !== null ? (searchFocusIndex + 1) % searchFoundCount : 0,
    })
  }

  toggleNodeExpansion = (expanded) => {
    this.setState((prevState) => ({
      treeData: toggleExpandedForAll({ treeData: prevState.treeData, expanded }),
    }))
  }
  handleChange = (node) => {
    if (node.action === 'addPortfolio') {
      console.log(node.title, node.action)
      //mui.onAddAction(node.title);
    } else if (node.action === 'editPortfolio') {
      console.log(node.title, node.action)
      //mui.onEditAction(node.title);
    } else if (node.action === 'deletetree') {
      console.log(node.title, node.action)
      //mui.onDeleteAction(node.title);
    } else if (node.action === 'deletenode') {
      console.log(node.title, node.action)
      //mui.onDeleteAction(node.title);
    }
  }

  render() {
    const { treeData, searchString, searchFocusIndex, searchFoundCount } = this.state

    return (
      <div className="wrapper">
        <div className="bar-wrapper">
          <button onClick={this.toggleNodeExpansion.bind(this, true)}>Expand all</button>
          <button className="collapse" onClick={this.toggleNodeExpansion.bind(this, false)}>
            Collapse all
          </button>
          <label style={{ fontSize: '90%' }}>Search: </label>
          <input onChange={this.handleSearchOnChange} />
          <button className="previous" onClick={this.selectPrevMatch}>
            Previous
          </button>
          <button className="next" onClick={this.selectNextMatch}>
            Next
          </button>
          <label>
            {searchFocusIndex} / {searchFoundCount}
          </label>
        </div>
        <ThemeProvider theme={mytheme}>
          <div style={{ width: '100%' }}>
            <SortableTree
              rowHeight={100}
              treeData={treeData}
              onChange={this.handleTreeOnChange}
              isVirtualized={false}
              theme={treeTheme}
              searchQuery={searchString}
              searchFocusOffset={searchFocusIndex}
              canDrag={({ node }) => !node.noDragging}
              canDrop={({ nextParent }) => !nextParent || !nextParent.noChildren}
              searchFinishCallback={(matches) =>
                this.setState({
                  searchFoundCount: matches.length,
                  searchFocusIndex: matches.length > 0 ? searchFocusIndex % matches.length : 0,
                })
              }
              generateNodeProps={({ node }) => ({
                buttons: [<MoreMenu key="more-menu" row={node} handleChange={this.handleChange} />],
              })}
            />
          </div>
        </ThemeProvider>
      </div>
    )
  }
}

const rootElement = document.getElementById('root')
ReactDOM.render(<App />, rootElement)
